import '../css/popup.css';

//loading start
document.getElementById("loading").contentDocument.write("loading");

const params = new URL(window.location.href);

let defaultStyle = {
    width: params.searchParams.get("width") + "px",
    height: params.searchParams.get("height") + "px",
    margin: "0",
    padding: "0"
};

let iframe = document.createElement("iframe");
iframe.setAttribute("scrolling", false);
iframe.setAttribute("seamless", true);
iframe.style.cssText = 'display: block; width: 100%; height: 100%; margin: 0; padding: 0; position: fixed; top: 0; left: 0; border: 0; z-index: 1000';
iframe.src = decodeURIComponent(params.searchParams.get("url"));
Object.assign(document.body.style, defaultStyle);
document.body.appendChild(iframe);
//loading end
setTimeout(function() {
    document.getElementById("loading").contentDocument.close();
}, 5000);

chrome.runtime.onMessage.addListener((message, ...arg) => {
    console.log ('popup message', message, arg);
    if (message.action == 'close') {
        window.close();
    } else if (message.action == 'setStyle') {
        Object.assign(document.body.style, message.style);
        //loading end
        document.getElementById("loading").contentDocument.close();
    }
});